import "core-js/modules/es.array.push.js";
import $ from 'jquery';
import { useStore } from 'vuex';
import router from '../router/index';
import { onMounted, reactive, toRefs } from 'vue';
import moment from 'moment/moment';
export default {
  setup() {
    const vue = reactive({
      tableData: [],
      table: [],
      loading: false,
      dialogVisible: false,
      index: 0,
      sou: '',
      select: 'Name',
      count: '',
      page: 1
    });

    const editUser = i => {
      vue.dialogVisible = true;
      vue.index = i;
    };

    const updata = () => {
      vue.dialogVisible = false;
      $.ajax({
        url: 'https://so.beink.cn/user/account/updata/',
        type: 'post',
        headers: {
          Authorization: "Bearer " + store.state.token
        },
        data: {
          id: vue.tableData[vue.index].id,
          account: vue.tableData[vue.index].account,
          backimg: vue.tableData[vue.index].backimg
        },

        success(res) {
          console.log(res);
        },

        error(res) {
          console.log(res);
        }

      });
    };

    const query = () => {
      if (vue.sou === '') {
        vue.tableData = vue.table;
      } else {
        if (vue.select === 'Name') {
          vue.tableData = vue.table.filter(i => {
            return i.account.indexOf(vue.sou) != -1;
          });
        } else {
          vue.tableData = vue.table.filter(i => {
            return i.id == vue.sou;
          });
        }
      }
    };

    let handleDelete = a => {
      let f = confirm("确认删除" + vue.tableData[a].account);

      if (f) {
        $.ajax({
          url: 'https://so.beink.cn/user/admin/delete/',
          type: 'post',
          headers: {
            Authorization: "Bearer " + store.state.token
          },
          data: {
            id: vue.tableData[a].id
          },

          success(res) {
            if (res === "true") aaa();
          },

          error(res) {
            console.log(res);
          }

        });
      }
    };

    let aaa = () => {
      $.ajax({
        url: 'https://so.beink.cn/user/admin/list/',
        type: 'post',
        headers: {
          Authorization: "Bearer " + store.state.token
        },
        data: {
          page: vue.page
        },

        success(res) {
          vue.count = res.count;
          vue.tableData = res.records;

          for (let i in vue.tableData) {
            vue.tableData[i].date = moment(vue.tableData[i].date).utcOffset(480).format('YYYY-MM-DD');
          }

          vue.loading = false;
          vue.table = vue.tableData;
        },

        error(res) {
          console.log(res);
        }

      });
    };

    const ppage = i => {
      vue.page += i;
      aaa();
    };

    const handleCurrentChange = number => {
      vue.page = number;
      aaa();
    };

    const store = useStore();
    onMounted(() => {
      const jwt = localStorage.getItem("jwt");

      if (jwt) {
        store.commit("updateToken", jwt);
        store.dispatch("getinfo", {
          success() {
            aaa();
          },

          error() {
            router.push({
              name: 'main'
            });
          }

        });
      }
    });
    return { ...toRefs(vue),
      handleDelete,
      editUser,
      query,
      ppage,
      handleCurrentChange,
      updata
    };
  }

};